import { CommonModule } from "@angular/common";
import { NgModule } from "@angular/core";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { MatDividerModule } from "@angular/material/divider";
import { MatIconModule } from "@angular/material/icon";
import { MatLegacyButtonModule as MatButtonModule } from "@angular/material/legacy-button";
import { MatLegacyCheckboxModule as MatCheckboxModule } from "@angular/material/legacy-checkbox";
import { MatLegacyFormFieldModule as MatFormFieldModule } from "@angular/material/legacy-form-field";
import { MatLegacyInputModule as MatInputModule } from "@angular/material/legacy-input";
import { MatLegacySelectModule as MatSelectModule } from "@angular/material/legacy-select";
import { RouterModule } from "@angular/router";
import { TeiTextTranslatePipeModule } from "@tei/core-translate";
import { AvatarModule, BreadCrumbModule, LoadingModule, SettingsListModule } from "@tei/material";
import { PrintReceiptSetupService } from "../../@services/print-receipt-setup.service";
import { PrintReceiptSetupModule } from "../../components/print-receipt-setup/print-receipt-setup.module";
import { RightSideActionBarTerminalModule } from "../../components/right-side-action-bar-terminal/right-side-action-bar-terminal.module";
import { FBApprovalSlipSetupComponent } from "./fb-approval-slip-setup.component";

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatIconModule, MatCheckboxModule, MatButtonModule, MatSelectModule, MatDividerModule, MatInputModule, MatFormFieldModule];
const TEI_MATERIAL_MODULES = [BreadCrumbModule, SettingsListModule, LoadingModule, AvatarModule];
const COMPONENTS = [FBApprovalSlipSetupComponent];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
const Shared_MODULES = [PrintReceiptSetupModule];
const TEI_FB_ADMIN_MODULES = [
  RightSideActionBarTerminalModule
];
@NgModule({
  declarations: [COMPONENTS],
  imports: [
    RouterModule.forChild([{ path: '', component: FBApprovalSlipSetupComponent }]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...Shared_MODULES,
    ...TEI_FB_ADMIN_MODULES

  ],
  providers: [
    { provide: "IPrintReceiptSetupService", useClass: PrintReceiptSetupService }
  ]
})

export class FBApprovalSlipSetupModule { }
