import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService } from '@tei/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { firstValueFrom } from 'rxjs';
import { GetPrintReceiptSetupResponse, ReceiptTypeEnum } from '../../@intermediate/print-receipt-setup/models/get-print-receipt-setup.model';
import { Language, PrintReceiptSetupForm } from '../../@intermediate/print-receipt-setup/models/print-receipt-setup.model';
import { IPrintReceiptSetupService } from '../../@intermediate/print-receipt-setup/print-receipt-setup.interface';
@Component({
  selector: 'tei-food-and-beverage-admin-approval-slip-setup',
  templateUrl: './fb-approval-slip-setup.component.html',
  styleUrls: ['./fb-approval-slip-setup.component.scss']
})
export class FBApprovalSlipSetupComponent implements OnInit
{
  Logo: string;
  posID: string;
  languages: Language[];
  ReceiptTypeEnum = ReceiptTypeEnum;
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('FBADMINISTRATION.NAVIGATION.FOOD_AMP_BEVERAGE'),
      relativeUrl: '../../../../../'
    },
    {
      title: this.translate.instant('FBADMINISTRATION.TERMINALS_CONFIGS.TERMINALS_TABLE.TERMINALS'),
      relativeUrl: '../../../../'
    },
    {
      title: this.translate.instant('FBADMINISTRATION.TERMINALS_CONFIGS.TERMINAL_PRINTING.PRINTING'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('FBADMINISTRATION.PRINT_APPROVAL_SLIP_SETUP.APPROVAL_SLIP_SETUP'),
      relativeUrl: ''
    }
  ];
  settingForm: FormGroup<PrintReceiptSetupForm>;
  constructor(@Inject('IPrintReceiptSetupService') private printReceiptSetupService: IPrintReceiptSetupService, public manageLoadingService: ManageLoadingService,
    private snackBar: SnackBarService, private translate: TranslateService, private activatedRoute: ActivatedRoute,
  )
  {

  }

  ngOnInit(): void
  {
    this.posID = this.activatedRoute.snapshot.paramMap.get('posID');
    this.loadData();
  }

  async loadData(): Promise<void>
  {
    try
    {
      this.manageLoadingService.startLoading();
      const response = await firstValueFrom(this.printReceiptSetupService.Get({ POSID: this.posID, ReceiptType: ReceiptTypeEnum.Approval }));
      this.languages = response.Languages;
      this.createFrom(response);

    }
    catch (error)
    {
      console.error(error);
    }
    finally
    {
      this.manageLoadingService.finishLoading();
    }

  }

  createFrom(response: GetPrintReceiptSetupResponse)
  {
    this.settingForm = new FormGroup<PrintReceiptSetupForm>({
      LanguageCode: new FormControl<string>(response.TerminalSetupReceipt?.LanguageCode ?? this.languages[0].Code, [Validators.required]),
      Address: new FormControl<string>(response.TerminalSetupReceipt?.Address),
      City: new FormControl<string>(response.TerminalSetupReceipt?.City),
      Footer: new FormControl<string>(response.TerminalSetupReceipt?.Footer),
      Header: new FormControl<string>(response.TerminalSetupReceipt?.Header),
      LogoImage: new FormControl<string>(null),
      LogoUrl: new FormControl<string>(response.TerminalSetupReceipt?.LogoUrl, response.TerminalSetupReceipt?.IncludeLogo ? [Validators.required] : []),
      LogoUID: new FormControl<string>(response.TerminalSetupReceipt?.LogoUID),
      Name: new FormControl<string>(response.TerminalSetupReceipt?.Name),
      Phone: new FormControl<string>(response.TerminalSetupReceipt?.Phone),
      PostalCode: new FormControl<string>(response.TerminalSetupReceipt?.PostalCode),
      State: new FormControl<string>(response.TerminalSetupReceipt?.State),
      QRCodeContent: new FormControl<string>({ value: response.TerminalSetupReceipt?.QRCodeContent, disabled: !response.TerminalSetupReceipt?.IncludeQRCode }, [Validators.pattern(/^(https?:\/\/[^\s$.?#].[^\s]*)|(.+)$/)]),
      IncludeQRCode: new FormControl<boolean>(response.TerminalSetupReceipt?.IncludeQRCode),
      IncludeLogo: new FormControl<boolean>(response.TerminalSetupReceipt?.IncludeLogo),
    });
  }

  async saveSetting(): Promise<void>
  {
    try
    {
      this.manageLoadingService.startLoading();
      const request = {
        ...this.settingForm.value,
        POSID: this.posID,
        ReceiptType: ReceiptTypeEnum.Approval,

      };
      await firstValueFrom(this.printReceiptSetupService.Post(this.convertBodyRequestToFormData(request)));
      this.snackBar.showSuccess(this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.SELECT_PI_SETUP.SAVED_SUCCESSFULLY'), '', null, 'left');
    }
    catch (error)
    {
      console.error(error);
    }
    finally
    {
      this.manageLoadingService.finishLoading();
    }
  }

  convertBodyRequestToFormData(bodyRequest: { [key in string | number]: any; } = {}): FormData
  {
    const formData = new FormData();
    Object.keys(bodyRequest).forEach(key =>
    {

      if (bodyRequest[key] instanceof Blob)
      {
        const fileExtension = bodyRequest[key]?.type.split('/')?.[1] ?? 'jpg';
        const fileName = key + "." + (fileExtension === 'x-icon' ? 'ico' : fileExtension);
        formData.append(key, bodyRequest[key], fileName);
      }
      else if (bodyRequest[key] != null)
      {
        formData.append(key, bodyRequest[key]);
      }
    });
    return formData;

  }
}

